import {
  mapMutations,
  mapGetters
} from 'vuex';

export default {
  layout: 'admin',
  components: {

  },
  data() {
    return {
      table: {
        max: 10
      },
      page: 1,
      titleDialog: 'Tambah Pemda',
      search: '',
      isUpdate: false,
      tambahDialog: false,
      btnLoader: false,
      files: [],
      form: {
        nama: '',
        foto: null,
        aktif: true
      }
    }
  },
  mounted() {
    this.$store.dispatch('goverment/getAll', {});
  },
  methods: {
    searchData(){
      this.$store.dispatch('goverment/getAll', {
        search: this.search
      });
    },
    edit(data) {
      this.form.nama = data.nama
      this.form.id = data.id
      this.form.aktif = data.aktif
      this.files.push({
        name: '',
        url: data.foto_url
      })
      this.tambahDialog = true
      this.titleDialog = 'Edit Pemerintah Daerah'
      this.isUpdate = true
    },
    resetForm() {
      this.form = {
        nama: '',
        foto: null,
        aktif: true
      }
      this.files = [];
      this.isUpdate = false
    },
    handleCurrentChange(val) {
      this.$store.commit('goverment/setPage', val)
      this.$store.dispatch('goverment/getAll', {});
    },
    onSubmit(type = 'store') {
      this.btnLoader = true
      let formData = new FormData()
      formData.append("nama", this.form.nama)
      formData.append("aktif", this.form.aktif ? 1 : 0)
      if (this.form.foto !== null) {
        formData.append("foto", this.form.foto)
      }
      let url = "/goverment/store";
      if (type == 'update') {
        url = `/goverment/${this.form.id}/update`
      }

      this.$axios.post(url, formData).then(resp => {
        if (resp.data.success) {
          this.$notify.success({
            title: 'Success',
            message: `Berhasil ${type == 'store' ? 'Menambah' : 'Mengubah'} Goverment`
          })
          this.resetForm()
          this.tambahDialog = false
          this.$store.dispatch('goverment/getAll', {});
        }
      }).finally(() => {
        this.btnLoader = false
      }).catch(err => {
        let error = err.response.data.data
        if (error) {
          this.showErrorField(error)
        } else {
          this.$notify.error({
            title: 'Error',
            message: err.response.data.message
          })
        }
      })
    },
    handleChangeFile(file, fileList) {
      this.form.foto = file.raw
    },
    deleteGoverment(id) {
      this.$swal({
        title: 'Perhatian!',
        text: "Apakah anda yakin ingin menghapus data ini?",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Ya',
        cancelButtonText: 'Batal'
      }).then((result) => {
        if (result.isConfirmed) {
          this.$axios.delete(`/goverment/${id}/delete`).then(resp => {
            if (resp.data.success) {
              this.$notify.success({
                title: 'Success',
                message: 'Berhasil Menghapus Data'
              })
              this.tambahDialog = false
              this.$store.dispatch('goverment/getAll', {
                defaultPage: true
              });
            }
          }).finally(() => {
            //
          }).catch(err => {
            this.$notify.error({
              title: 'Error',
              message: err.response.data.message
            })
          })
        }
      })
    }
  },
  computed: {
    ...mapGetters("goverment", [
      'getGoverments',
      'getLoader'
    ])
  },
  watch: {
    getGoverments(newValue, oldValue) {

    },
    search(newValue, oldValue) {
      // this.$store.dispatch('goverment/getAll', {
      //   search: newValue
      // });
    },
    page(newValue, oldValue) {
      this.$store.commit('goverment/setPage', newValue)
      this.$store.dispatch('goverment/getAll', {});
    }
  },
}

